<template>
    <section class="fxt-template-animation fxt-template-layout29">
        <div class="container-fluid">
            <div class="row">
                <VegasComponent></VegasComponent>
                <div class="col-md-6 col-12 fxt-bg-color">
                    <div class="fxt-content">
                        <div class="fxt-header">
                            <a href="index.html" class="fxt-logo"><img src="img/logo-29.svg" alt="Logo"></a>
                        </div>
                        <div class="fxt-form">
                            <div class="fxt-transformY-50 fxt-transition-delay-1">
                                <h2>Olvide mi contraseña</h2>
                            </div>
                            <div class="fxt-transformY-50 fxt-transition-delay-2">
                                <p>Para recuperar tu contraseña por favor ingresar su correo.</p>
                            </div>
                            <form method="POST">
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-1">
                                        <input type="email" class="form-control" v-model="Correo" name="email" placeholder="Correo electrónico" required="required">
                                        <i class="flaticon-envelope"></i>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-3">
                                        <button type="submit" v-on:click="RestableceContra" class="fxt-btn-fill">Enviar a correo electrónico</button>
                                    </div>
                                </div>
                            </form>
                        </div>
                        <div class="fxt-footer">
                            <div class="fxt-transformY-50 fxt-transition-delay-8">
                                <h3>O Inicia sesión con:</h3>
                            </div>
                            <ul class="fxt-socials">
                                <li class="fxt-google fxt-transformY-50 fxt-transition-delay-11">
                                    <a href="#" title="google"><i class="fab fa-google-plus-g"></i></a>
                                </li>

                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    import VegasComponent from "../components/VegasComponent";
    export default {
        name: "RestableceContra",
        components:{
            VegasComponent
        },
        data(){
            return {
                Correo:''
            }
        },methods:{
            RestableceContra:function (e) {
                e.preventDefault();
                var requestOptions = {
                    method: 'POST',
                    redirect: 'follow'
                };

                fetch("http://servicios.anamayas.com/lib/reseteaUsuario.php?CorreoStr="+this.Correo, requestOptions)
                    .then(response => response.json())
                    .then(result => console.log(result))
                    .catch(error => console.log('error', error));
                alert("Se restablecio contraseña de "+this.Correo)
            }
        }
    }
</script>

