<template>
    <section class="fxt-template-animation fxt-template-layout29">
        <div class="container-fluid">
            <div class="row">
               <VegasComponent></VegasComponent>
                <div class="col-md-6 col-12 fxt-bg-color">
                    <div class="fxt-content">
                        <div class="fxt-header">
                            <router-link to="/" lass="fxt-logo"><img src="img/logo-29.svg" alt="Logo"></router-link>
                        </div>
                        <div class="fxt-form">
                            <div class="fxt-transformY-50 fxt-transition-delay-1">
                                <h2>Iniciar sesión</h2>
                            </div>
                            <div class="fxt-transformY-50 fxt-transition-delay-2">
                                <p>Por favor inicia sesión para ingresar al sitio</p>
                            </div>
                            <form method="POST">
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-4">
                                        <input type="email" class="form-control" v-model="username" name="email" placeholder="Correo electrónico" required="required">
                                        <i class="flaticon-envelope"></i>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-5">
                                        <input type="password" class="form-control" v-model="password" name="password" placeholder="Password" required="required">
                                        <i class="flaticon-padlock"></i>
                                    </div>
                                    <br>
                                    <label class="checkcita"><input v-model="Recordar" type="checkbox" id="cbox1" value="first_checkbox" class="checkCitas"> Recordar datos</label>
                                </div>
                                <div v-show="AlertInicio" class="alert alert-danger" role="alert">
                                    Usuario y/o Contraseña incorectos, favor de validar
                                </div>
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-6">
                                        <div class="fxt-content-between">
                                            <button v-on:click="ValidaUsuario" type="submit" class="fxt-btn-fill">Iniciar sesión</button>
                                            <router-link to="/RestableceContra" class="switcher-text2" >Olvidaste tu contraseña?</router-link>
                                        </div>
                                    </div>
                                </div>
                            </form>
                        </div>
                        <div class="fxt-footer">
                            <div class="fxt-transformY-50 fxt-transition-delay-8">
                                <h3>O Inicia sesión con:</h3>
                            </div>
                            <ul class="fxt-socials">
                                <li class="fxt-google fxt-transformY-50 fxt-transition-delay-11">
                                    <a href="Google" title="google"><i class="fab fa-google-plus-g"></i></a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    'use strict';
    import { mapActions } from 'vuex'
    import VegasComponent from "../components/VegasComponent";
    export default {
        name: 'Login',
        components: {
            VegasComponent,
        },
        data(){
            return{
                username:'',
                password:'',
                Recordar:false,
                AlertInicio:false
            }
        },beforeCreate() {
            let temporal=localStorage.getItem("AmayaRecordaDatos");
            if(temporal!=null || temporal!=undefined){
                if(temporal.Recordar==true){
                    this.$router.push('/Citas')
                }
            }
        },
        methods: {
            ...mapActions('login', {
                login: 'login'
            }),
            ValidaUsuario: function (e) {
                e.preventDefault();
                var requestOptions = {
                    method: 'POST',
                    redirect: 'follow'
                };
                fetch("http://servicios.anamayas.com/lib/validaUsuario.php?CorreoStr="+this.username+"&passStr="+this.password, requestOptions)
                    .then(response => response.json())
                    .then(result => {
                        console.log(result)
                        if(result.Resultado==='0'){
                            if(this.Recordar){
                                let temp={
                                    User:this.username,
                                    Pass:this.Password,
                                    Recordar:true
                                }
                                localStorage.setItem('AmayaRecordaDatos',JSON.stringify(temp));
                            }else{
                                let temp={
                                    User:this.username,
                                    Pass:this.Password
                                }
                                sessionStorage.setItem('AmayaRecordaDatos',JSON.stringify(temp));
                            }
                            const { username, password } = this;
                            this.login({ username, password });
                            this.$router.push('/Citas')
                        }else{
                            this.AlertInicio=true;
                        }
                    })
                    .catch(error => console.log('error', error));
            }
        }
    };
</script>

