<template>
<div>
    <section class="d-flex justify-content-center" style="padding-top: 50px;" >
        <div class="container">
                <div class="col-md-12 col-sm-12 col-lg-12 col-xl-12 ">
                <div ref="paypal" style="width: 100%;"></div>
            </div>
        </div>
    </section>
    <div v-show="Alert1" class="alert alert-warning alert-dismissible fade show" role="alert">
        <strong>Error al procesar el pago!</strong> Favor de contactar al administrador.
        <button v-on:click="Cerrar"  type="button" class="close" data-dismiss="alert" aria-label="Close">
            <span aria-hidden="true">&times;</span>
        </button>
    </div>
    <div v-show="Alert2" class="alert alert-warning alert-dismissible fade show" role="alert">
        <strong>Cita Generada</strong> Favor de Cerrar la ventana o hacer click en la x
        <button v-on:click="Cerrar2" type="button" class="close" data-dismiss="alert" aria-label="Close">
            <span aria-hidden="true">&times;</span>
        </button>
    </div>
</div>
</template>

<script>
    export default {
        name: "Payment",
        data: function() {
            return {
                Alert1:false,
                Alert2:false,
            };
        },
        props:{FolioCita:{
                type:String
            }},
        mounted: function() {
            const script = document.createElement("script");
            script.src = "https://www.paypal.com/sdk/js?client-id=AWGSW8osLSkkbZ_ZQkyApCxlqiiS2HiBuYXhtSs5A8dV7W_CXmxelQY6Wwt9NvJqvn93QB_py91iab0F&currency=MXN";
            script.addEventListener("load", this.setLoaded);
            document.body.appendChild(script);
        },
        created() {
            console.log(this.FolioCita)
        },
        watch: {
            FolioCita: function(newVal, oldVal) {
                this.setLoaded();
                console.log('Prop changed: ', newVal, ' | was: ', oldVal)
            }
        },
        methods: {
            Cerrar:function(){
                this.Alert1=false
            },
            Cerrar2:function(){
                this.Alert2=false;
                this.$router.push('/')
            },
            setLoaded: function() {
                console.log("FolioCita")
                console.log(this.FolioCita)
                if (this.FolioCita != null) {
                    this.loaded = true;
                    window.paypal.Buttons({
                        createOrder: (data, actions) => {
                            return actions.order.create({
                                purchase_units: [
                                    {
                                        description: "Cita Anamaya",
                                        amount: {
                                            currency_code: "MXN",
                                            value: 1200
                                        }
                                    }
                                ]
                            });
                        },
                        onApprove: async (data, actions) => {
                            const order = await actions.order.capture();
                            console.log(order);
                            this.ConfirmaCita(data);
                        },
                        onError: err => {
                            console.log(err);
                            this.Alert1=true;
                        }
                    })
                        .render(this.$refs.paypal);
                }
            },
            ConfirmaCita: function(data){

                var requestOptions = {
                    method: 'POST',
                    redirect: 'follow'
                };

                fetch("http://servicios.anamayas.com/lib/confirmaCita.php?" +
                    "IdFolio=" +this.FolioCita+
                    "&Descripcion=FolioPaypal"+data.id, requestOptions)
                    .then(response => response.json())
                    .then(result => {
                        console.log(result);
                        this.FolioCita=result.Folio;
                        this.Alert2=true;
                    })
                    .catch(error => console.log('error', error));
            },
        }
    };
</script>

<style scoped>

</style>