<template>
    <div>
    <div v-show="Genera">
        <section class="beautypress-booking-section beautypress-bg beautypress-padding-bottom parallax-bg" data-parallax="scroll" data-image-src="img/get_appoinment.jpg">
            <div class="container">
                <div class="beautypress-section-headinig">
                    <h2>Quieres visitarnos?</h2>
                    <h3>Agenda una cita</h3>
                </div>

                <div class="row">
                    <div class="col-md-12 col-sm-12 col-lg-6 col-xl-6">
                        <div class="beautypress-booking-text-wraper">
                            <div class="beautypress-booking-content-text beautypress-border beautypress-version-3">
                                <div class="beautypress-booking-text">
                                    <h2>Horario de consultas</h2>
                                    <h3></h3>
                                    <div class="beautypress-icon-bg-text">
                                        <p> Horarios sujetos a disponibilidad </p>
                                    </div>
                                    <ul>
                                        <li>L - V: 9:00am - 07:00pm</li>
                                        <li>S : 90:00am - 12:00pm</li>
                                        <li>D: Cerrado</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-12 col-sm-12 col-lg-6 col-xl-6">
                        <div class="beautypress-booking-form-wraper">
                            <form action="#" method="POST" id="beautypress-booking-form">
                                <input type="hidden" name="action" value="send_appointment_form" />

                                <div class="beautypress-service-and-date">
                                    <h2>Selecciona la fecha de tu cita</h2>
                                    <div class="form-group first-name-group">
                                        <input type="text" name="first-name" class="form-control" v-model="Nombre" placeholder="Nombre(s)">
                                    </div>
                                    <div class="beautypress-spilit-container">
                                        <div class="form-group first-name-group">
                                            <input type="text" name="first-name" class="form-control" v-model="ApellidoPaterno" placeholder="Apellido Paterno">
                                        </div>
                                        <div class="form-group first-name-group">
                                            <div class="input-group">
                                                <input type="text" name="first-name" class="form-control" v-model="ApellidoMaterno" placeholder="Apellido Materno">
                                            </div>
                                        </div>
                                    </div>
                                    <div class="beautypress-select">
                                        <div class="input-group">
                                            <select name="appointment-service" v-model="TipoConsulta" id="appointment-service" class="form-control">
                                                <option value="">Selecciona la consulta</option>
                                                <option value="1">Canalización</option>
                                                <option value="2">Sanación</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div class="beautypress-spilit-container">
                                        <div class="beautypress-date-select beautypress-select">
                                            <div class="input-group">
                                                <datepicker v-model="Fecha" :open-date="DateInicio" :disabled-dates="DiasInabiles"  :format="customFormatter" @selected="ObtieneHorarios" placeholder="Fecha de visita" style="height: 100%;width: 100%"></datepicker>
                                            </div>
                                        </div>
                                        <div class="beautypress-select">
                                            <div class="input-group">
                                                <select name="appointment-time" v-model="TipoSesion" class="form-control">
                                                    <option value="">Selecciona tipo de sesión</option>
                                                    <option value="1">Presencial</option>
                                                    <option value="2">A distancia</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div class="beautypress-select">
                                            <div class="input-group">
                                                <select name="appointment-time" v-model="Horario" class="form-control">
                                                    <option value="">Selecciona el horario</option>
                                                    <option value="09:00 AM">09:00 AM</option>
                                                    <option value="10:00 AM">10:00 AM</option>
                                                    <option value="01:00 AM">11:00 AM</option>
                                                    <option value="05:00 PM">05:00 PM</option>
                                                    <option value="06:00 PM">06:00 PM</option>
                                                    <option value="07:00 PM">07:00 PM</option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div v-show="Alert" class="alert hidden" id="beautypress-form-msg">
                                    <div class="alert alert-warning" role="alert">
                                        {{MensajeAlert}}
                                    </div>
                                </div>
                                <div class="beautypress-personal-information">
                                    <h2>Quiero contactar a un ser querido</h2>
                                    <div class="beautypress-spilit-container">

                                        <div class="form-group first-name-group">
                                            <input type="text" name="first-name" class="form-control" v-model="NomSerQuerido" placeholder="Nombre completo....">
                                        </div>
                                        <div class="beautypress-date-select beautypress-select">
                                            <div class="input-group">
                                                <datepicker v-model="FechaSerQuerido" :open-date="new Date()" :format="customFormatter" placeholder="Fecha de nacimiento" style="height: 100%;width: 100%"></datepicker>
                                            </div>
                                        </div>

                                    </div>
                                    <div>
                                        <div class="form-group">
                                            <input type="text" name="first-name" class="form-control" v-model="Parentesco" placeholder="Parentesco">
                                        </div>
                                    </div>
                                    <div class="form-group button-group">
                                        <label class="checkcita"><input v-model="Acepto" type="checkbox" id="cbox1" value="first_checkbox" class="checkCitas"> Acepto totalmente sin prejuicio alguno, ni responsabilidad legal, todos los comentarios vertidos durante mi canalizaciones a titulo personal que me expresen los sanadores de ANAMAYA´s.</label>
                                        <input type="submit" name="submit" value="Enviar" v-on:click="Validaciones" id="beautypress-submit">
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
        <div v-show="Pago">
            <Payment :folio-cita="FolioCita"></Payment>
        </div>
    </div>
</template>

<script>
    import Datepicker from 'vuejs-datepicker';
    import Payment from "../components/Payment";
    export default {
        name: "GeneraCitas",
        components: {
            Datepicker,//01/25/2022
            Payment
        },
        props: {
            ShowVer:{
                type: Number,
                required: true,
                Default:2
            }},
        data() {
            return {
                FolioCita:null,
                IdUsuaro:null,
                Genera:true,
                Pago:false,
                DiasInabiles: {},
                customFormatter:'MM/dd/yyyy',
                FechasHabiles: {},
                Nombre:'',
                ApellidoPaterno:'',
                ApellidoMaterno:'',
                TipoConsulta:'',
                Fecha:'',
                TipoSesion:'',
                Horario:'',
                NomSerQuerido:'',
                FechaSerQuerido:'',
                DatosCliente:null,
                Acepto:false,
                Alert:false,
                MensajeAlert:'',
                Parentesco:'',
                DateInicio:null
            }
        },
        beforeCreate() {
            let temp =new Date()//.setDate(new Date().getDate() - 5)
            this.DateInicio=temp.setDate(new Date().getDate() - 1)
            let templat=new Date(this.DateInicio);

            console.log(templat)
            console.log("Date")
            var requestOptions = {
                method: 'POST',
                redirect: 'follow'
            };
            fetch("http://servicios.anamayas.com/lib/consultaDisponibilidad.php", requestOptions)
                .then(response => response.json())
                .then(result => {
                    var temporal=[]
                    result.forEach(element=>{
                        let datetemp=element.dia.split('-');
                        temporal.push(new Date(datetemp[0],datetemp[1]-1,datetemp[2]))
                    })
                    this.FechasHabiles={
                        dates: temporal,
                        includeDisabled: true
                    }
                    console.log(result)
                    fetch("http://servicios.anamayas.com/lib/ConsultaDiasNoLaborables.php", requestOptions)
                        .then(response => response.json())
                        .then(result => {
                            console.log(result)
                            temporal=[]
                            result.forEach(element=>{
                                let datetemp=element.DiaNoLaboral.split('-');
                                temporal.push(new Date(datetemp[0],datetemp[1]-1,datetemp[2]))
                            })
                            let _this=this;
                            this.DiasInabiles={
                                customPredictor: function(date) {
                                    const found = _this.FechasHabiles.dates.find(element => {
                                        if(date.getFullYear()===element.getFullYear()&&date.getMonth()===element.getMonth()&&date.getDate()===element.getDate())
                                        {
                                            return element;
                                        }
                                    })
                                    if(found===undefined){
                                        return true;
                                    }
                                }
                            }
                        })
                        .catch(error => console.log('error', error));
                })
                .catch(error => console.log('error', error));
        },
        created() {
            let temporal=JSON.parse(localStorage.getItem("AmayaRecordaDatos"));
            if(temporal==null){
                console.log("Entro")
                temporal=JSON.parse(sessionStorage.getItem("AmayaRecordaDatos"));
            }
            console.log(this.ShowVer)
            if(temporal==null && this.ShowVer===1) {
                this.$router.push('/Login')
            }else{
                this.Genera=false;
            }
            console.log("Valor de temporal")
            console.log(temporal);
            if(temporal!=null) {
                this.Genera=true;
                var requestOptions = {
                    method: 'POST',
                    redirect: 'follow'
                };
                fetch("http://servicios.anamayas.com/lib/consultaUsuario.php?correo=" + temporal.User, requestOptions)
                    .then(response => response.json())
                    .then(result => {
                        this.DatosCliente = result;
                        this.Nombre = result.Nombre;
                        this.ApellidoPaterno = result.ApellidoPaterno;
                        this.ApellidoMaterno = result.ApellidoMaterno;
                        this.IdUsuaro = result.Id;
                        console.log(result)
                    })
                    .catch(error => console.log('error', error));
            }
        },
        methods:{
            Validaciones:function(e){
                e.preventDefault();
                if(this.Nombre===''){
                    this.MensajeAlert='Favor de ingresar su Nombre';
                    this.Alert=true;
                }
                else if(this.ApellidoMaterno===''){
                    this.MensajeAlert='Favor de ingresar su Apellido Materno';
                    this.Alert=true;
                }
                else if(this.ApellidoPaterno===''){
                    this.MensajeAlert='Favor de ingresar su Apellido Paterno';
                    this.Alert=true;
                }
                else if(this.TipoConsulta===''){
                    this.MensajeAlert='Favor de seleccionar tipo consulta';
                    this.Alert=true;
                }
                else if(this.Fecha===''){
                    this.MensajeAlert='Favor de ingresar Fecha';
                    this.Alert=true;
                }
                else if(this.TipoSesion===''){
                    this.MensajeAlert='Favor de Seleccionar Tipo Sesion';
                    this.Alert=true;
                }else if(this.Horario===''){
                    this.MensajeAlert='Favor de seleccionar un Horario';
                    this.Alert=true;
                }else{
                    this.GeneraCita();
                }
            },
            GeneraCita:function(){
                if(this.Acepto){
                    var requestOptions = {
                        method: 'POST',
                        redirect: 'follow'
                    };
                    let temp=this.Fecha.getFullYear()+'-'+this.Fecha.getMonth()+1+'-'+this.Fecha.getDate();
                    let FchaParentesco=this.Fecha.getFullYear()+'-'+this.Fecha.getMonth()+1+'-'+this.Fecha.getDate();
                    console.log(temp)
                    fetch("http://servicios.anamayas.com/lib/creaCita.php?" +
                            "IdUsuario="+this.IdUsuaro+
                            "&IdTipoCita=" +this.TipoConsulta+
                            "&Fecha=" +temp+
                            "&Hora=" +this.Horario+
                            "&ContactarPersona=" +this.NomSerQuerido+
                            "&ParentescoPersona=" +this.Parentesco+
                            "&FecNacPersona="+FchaParentesco, requestOptions)
                        .then(response => response.json())
                        .then(result => {
                            if(result.Resultado=="Exitoso"){
                                this.FolioCita=result.Folio;
                                this.Genera=false;
                                this.Pago=true;
                                window.scrollTo(0, 0);
                                console.log(result);
                            }
                        })
                        .catch(error => console.log('error', error));

                }else{
                    this.MensajeAlert='Terminos y condiciones No aceptadas';
                    this.Alert=true;
                }
            },
            ObtieneHorarios:function(){
                console.log("Entro a Fechas");
                let temp=this.FechasHabiles.indexOf(this.Fecha);
                console.log(temp)
            }
        }
    }
</script>

<style scoped>

</style>