<template>
    <section class="fxt-template-animation fxt-template-layout29">
        <div class="container-fluid">
            <div class="row">
                <VegasComponent></VegasComponent>
                <div class="col-md-6 col-12 fxt-bg-color">
                    <div class="fxt-content">
                        <div class="fxt-header">
                            <a href="login-registro.html" class="fxt-logo"><img src="img/logo-29.svg" alt="Logo"></a>
                        </div>
                        <div class="fxt-form">
                            <div class="fxt-transformY-50 fxt-transition-delay-1">
                                <h2>Crear cuenta</h2>
                            </div>
                            <div class="fxt-transformY-50 fxt-transition-delay-2">
                                <p>Registrate para disfrutar de todo nuestro contenido</p>
                            </div>
                            <form method="POST">
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-1">
                                        <input type="text" class="form-control" name="name" v-model="NombreCompleto" placeholder="Nombre" required="required">
                                        <i class="flaticon-user"></i>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-1">
                                        <input type="text" class="form-control" name="name" v-model="ApellidoPaterno" placeholder="Apellido Paterno" required="required">
                                        <i class="flaticon-user"></i>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-1">
                                        <input type="text" class="form-control" name="name" v-model="ApellidoMaterno" placeholder="Apellido Materno" required="required">
                                        <i class="flaticon-user"></i>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-1">
                                        <input type="number" class="form-control" name="telefono" v-model="Telefono" placeholder="Teléfono" required="required">
                                        <i class="flaticon-telephone"></i>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-1">
                                        <input type="email" class="form-control" name="email" v-model="Correo" placeholder="Correo eléctronico" required="required">
                                        <i class="flaticon-envelope"></i>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-2">
                                        <input type="password" class="form-control" name="password" v-model="Password" placeholder="Password" required="required">
                                        <i class="flaticon-padlock"></i>
                                    </div>
                                </div>
                                <div v-show="Alert" class="alert alert-warning" role="alert">
                                    {{MensajeAlert}}
                                </div>
                                <div v-show="Alert2" class="alert alert-danger" role="alert">
                                    {{MensajeAlert}}
                                </div>
                                <div class="form-group">
                                    <div class="fxt-transformY-50 fxt-transition-delay-3">
                                        <button type="submit" v-on:click="Validaciones" class="fxt-btn-fill">Registrarse</button>
                                    </div>
                                </div>
                            </form>
                        </div>
                        <div class="fxt-footer">
                            <div class="fxt-transformY-50 fxt-transition-delay-8">
                                <h3>O Inicia Sesión con:</h3>
                            </div>
                            <ul class="fxt-socials">

                                <li class="fxt-google fxt-transformY-50 fxt-transition-delay-11">
                                    <a href="#" title="google"><i class="fab fa-google-plus-g"></i></a>
                                </li>

                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    import VegasComponent from "../components/VegasComponent";
    export default {
        name: 'Registro',
        components: {
            VegasComponent,
        },
        data(){
            return{
                NombreCompleto:'',
                ApellidoMaterno:'',
                ApellidoPaterno:'',
                Telefono:'',
                Correo:'',
                Password:'',
                Alert:false,
                Alert2:false,
                MensajeAlert:''
            }
        },
        methods:{
            Validaciones:function(e){
                e.preventDefault();
                if(this.NombreCompleto===''){
                    this.MensajeAlert='Favor de ingresar su Nombre Completo';
                    this.Alert=true;
                }
                else if(this.ApellidoMaterno===''){
                    this.MensajeAlert='Favor de ingresar su Apellido Materno';
                    this.Alert=true;
                }
                else if(this.ApellidoPaterno===''){
                    this.MensajeAlert='Favor de ingresar su Apellido Paterno';
                    this.Alert=true;
                }
                else if(this.Telefono===''){
                    this.MensajeAlert='Favor de ingresar un telefono';
                    this.Alert=true;
                }
                else if(this.Correo===''){
                    this.MensajeAlert='Favor de ingresar un correo';
                    this.Alert=true;
                }
                else if(this.Password===''){
                    this.MensajeAlert='Favor de ingresar una contraseña';
                    this.Alert=true;
                }else{
                    this.AltaUsuario();
                }
            },
            AltaUsuario:function(){
                var requestOptions = {
                    method: 'POST',
                    redirect: 'follow'
                };
                let URL="http://servicios.anamayas.com/lib/creaUsuario.php?Correo="+this.Correo+"&Pass="+this.Password+"&Nombre="+this.NombreCompleto+"&Telefono="+this.Telefono+"&ApellidoPaterno="+this.ApellidoPaterno+"&ApellidoMaterno="+this.ApellidoMaterno
                fetch( URL, requestOptions)
                    .then(response => response.json())
                    .then(result => {
                        console.log(result)
                        if(result[0].Resultado=='0'){
                            this.NombreCompleto=''
                            this.ApellidoMaterno=''
                            this.ApellidoPaterno=''
                            this.Telefono=''
                            this.Correo=''
                            this.Password=''
                            this.MensajeAlert='Registro Exitoso';
                            this.Alert=true;
                        }
                    })
                    .catch(error => {
                            this.MensajeAlert='Error al Crear usuario: '+error;
                            this.Alert2=true;
                    });

            }
        }
    };
</script>

