<template>
    <header v-show="currentRouteName" class="beautypress-header-section beautypress-version-1 beautypress-extra-css menu-skew bg-color-purple header-height-calc-minus navbar-fixed">
        <div class="container">
            <div class="beautypress-logo-wraper">
                <a href="" class="beautypress-logo beautypress-version-2  beautypress-version-4">
                    <img src="img/logo-v1-1.svg" alt="">
                </a>
            </div>
        </div>
        <div class="beautypress-header-top">
            <div class="container">
                <ul class="beautypress-simple-iocn-list beautypress-version-1">
                    <li><i class="xsicon icon-call"></i>+00 11 222 333 444</li>
                    <li><i class="xsicon icon-envelope"></i><a href="#">contacto@anamayas.com</a></li>
                    <li v-show="!Sesion" ><i class="fa fa-user"></i><a v-on:click="Carga('/Login')">Iniciar sesión</a></li>
                    <li v-show="Sesion" ><i class="fa fa-user"></i><a v-on:click="CerrarSesion('/')">Cerrar sesión</a></li>
                </ul>
            </div>
        </div>
        <div class="beautypress-main-header color-white">
            <div class="container">
                <nav class="xs_nav beautypress-nav beautypress-mega-menu">
                    <div class="nav-header">
                        <div class="nav-toggle"></div>
                    </div>
                    <div class="nav-menus-wrapper">
                        <ul class="nav-menu">
                            <li><router-link to="/">Inicio</router-link></li>
                            <li><router-link to="/About">¿Quiénes somos?</router-link></li>
                            <li><router-link to="/Servicios">¿Que hacemos?</router-link>
                                <ul class="nav-dropdown">
                                    <li><router-link to="/Reiki">Reiki</router-link></li>
                                    <li><router-link to="/Medium">Medium</router-link></li>
                                    <li><router-link to="/Pendulo">Péndulo</router-link></li>
                                    <li><router-link to="/TarotOCromancia">Tarot o Cartomancia</router-link></li>
                                    <li><router-link to="/TarotEspiritual">Tarot espiritual</router-link></li>
                                    <li><router-link to="/TarotAngeles">Tarot de los ángeles</router-link></li>
                                    <li><router-link to="/Biodescodificacion">Biodescodificación</router-link></li>
                                    <li><router-link to="/Biomagnetismo">Biomagnetismo</router-link></li>
                                    <li><router-link to="/CanalizacionAbierta">Canalizaciones abiertas y privadas</router-link></li>
                                    <li><router-link to="/Curso">Cursos</router-link></li>
                                </ul>
                            </li>
                            <li><router-link to="/Testimoniales">Testimoniales</router-link></li>
                            <li><router-link to="/Citas">Citas</router-link></li>
                            <li><router-link to="/Contacto">Contacto</router-link></li>
                        </ul>
                    </div>
                </nav>
            </div>
        </div>
    </header>
</template>

<script>
    export default {
        name: "NavBar",
        methods:{
          Carga:function (url) {
              this.$router.push(url);
          },
            CerrarSesion:function (url) {
                localStorage.removeItem("AmayaRecordaDatos");
                sessionStorage.removeItem("AmayaRecordaDatos");
                console.log(this.$route.name);
                if(this.$route.name!='Home') {
                    this.$router.push(url);
                }else{
                    window.location.reload();
                }
            }
        },
        computed: {
            Sesion(){
                let temporal=localStorage.getItem("AmayaRecordaDatos");
                if(temporal==null){
                    temporal=sessionStorage.getItem("AmayaRecordaDatos");
                    if(temporal!=null){
                        return true;
                    }
                }else{
                    return true;
                }
                return false;
            },
            currentRouteName() {
                if(this.$route.name==='Login'|| this.$route.name==='Registro' || this.$route.name==='RestableceContra'){
                    return false;
                }else{
                    return true;
                }
            }
        },
        watch:{
            $route (to, from){
                let sesion=localStorage.getItem('Sesion')
                console.log("Entro Aqui")
                console.log("Cambio", to, from);
                if(sesion==null){
                    console.log("Entro Aqui 1")
                        localStorage.setItem('Sesion',{
                            to:to.fullPath,
                            Actualizo:true
                        })
                    window.location.reload();
                }else{
                    console.log("Entro Aqui 2")
                    if(sesion.Actualizo!==true){
                        localStorage.setItem('Sesion',{
                            to:to.fullPath,
                            Actualizo:true
                        })
                        window.location.reload();
                    }else{
                        localStorage.setItem('Sesion',{
                            to:to.fullPath,
                            Actualizo:false
                        })
                    }
                }

            }
        }
    }
</script>

